
.articolo-title{
    margin: 40px 0px 30px 20px;
    font-weight: 600;
    color:black;
    font-size: 40px;
    line-height: 30px;
}

.articolo-image{
    width: 100vw;
    height: 60vh;
    margin-left: 20px;
    object-fit: contain;
}

.articolo-subtitle{
    margin: 40px 0px 10px 5vw;
    text-align: left;
    font-weight: 400;
    color:rgb(55, 51, 51);
    font-size: 30px;
}

.articolo-body{
    white-space: pre-line;
    margin: 20px 5vw 10px 5vw;
    font-weight: 400;
    color:black;
    font-size: 20px;
    line-height: 1.4;
    text-align: justify;
}

.articolo--tagTitle{
}

.articolo--tagContainer{
    margin-top: 50px;
    display: flex;
}

.articolo--allTags{
    font-weight: 600;
    text-align: center;
    margin-left: 5px;
}

@media only screen and (max-width: 700px) {
    .articolo-image{
        width: 100vw;
        height: 52vw;
        object-fit: fill;
        margin-top: 14vw;
        margin-left: 0px;
    }

    .articolo--info{
        display: flex;
        text-align: left;
        margin-left: 5vw;
        margin-top: 1vw;
        margin-bottom: 0vw;
        font-size: 4vw;
        font-weight: 600;
    }
    .articolo--tag{
        margin-left: 2vw;
    }
    .articolo-title{
        margin: 2vw 5vw 0vw 5vw;
        font-size: 7vw;
        line-height: 9vw;
    }

    .articolo-subtitle{
        margin: 0vw;
        padding: 3vw 5vw 2vw 5vw;
        color: rgb(62, 57, 57);
        font-size: 4.7vw;
        line-height: 1.3;
        text-align: justify;
    }
    .articolo--head{
        border-bottom: 4px solid #F1F1F1;
    }

    .articolo--share{
        margin: 2vw 5vw 5vw 5vw;
        display: flex;
        gap: 2vw;
    }

    .articolo-body{
        margin: 3vh 5vw 0px 5vw;
        font-size: 4.4vw;
        line-height: 1.6;
    }
    .articolo-body a:link{
        text-decoration: none;
    }
    .articolo-body a {
        color:rgb(234, 144, 0);
        font-weight: 700;
    }
    .articolo-body img {
        margin: 4vw 0vw 2vw 5vw;
        width: 80vw;
        height: auto;
        object-fit: fill;
    }
    .articolo-body blockquote {
        background: #f9f9f9;
        border-left: 2vw solid #ccc;
        margin: 1vw 2vw;
        padding: 4vw 2.5vw;
        display: flex;
        align-items: center;
    }
    .articolo-body blockquote p {
        display: inline;
        font-size: 4vw;
        font-style: italic;
    }

    iframe {
        margin: 4vw 0vw 2vw 5vw;
        width: 80vw;
        height: auto;
    }

    .articolo--share--bottom{
        margin: 5vw 5vw 0vw 5vw;
        display: flex;
        gap: 2vw;
    }

    .articolo--tagTitle{
    }
    
    .articolo--tagContainer{
        margin: 3vw 5vw 0vw 5vw;
        font-size: 3.5vw;
    }
    
    .articolo--allTags{
        color:rgb(234, 144, 0);
        font-weight: 600;
        text-align: center;
        margin-left: 1.5vw;
    }
    
}


